// src/lib/notifications.ts
import { supabase } from './supabaseClient';

interface NotificationData {
  type: 'invitation_received' | 'invitation_accepted' | 'invitation_rejected' | 'photo_uploaded' | 'message_received' | 'connection_established' | 'profile_updated';
  userId: string;
  organizationId: string;
  title: string;
  message: string;
  relatedId?: string;  // ‚Üê A√ëADIR este campo
  data?: any;
}

/**
 * Enviar una notificaci√≥n a un usuario
 */
export async function sendNotification({
  type,
  userId,
  organizationId,
  title,
  message,
  relatedId,  // ‚Üê A√ëADIR este par√°metro
  data = {}
}: NotificationData) {
  try {
    const { error } = await supabase
      .from('notifications')
      .insert({
        user_id: userId,
        organization_id: organizationId,
        type,
        title,
        message,
        related_id: relatedId,  // ‚Üê GUARDAR related_id en la base de datos
        data
      });

    if (error) {
      console.error('Error sending notification:', error);
      throw error;
    }

    console.log('Notification sent successfully');
  } catch (error) {
    console.error('Failed to send notification:', error);
    throw error;
  }
}

/**
 * Enviar notificaci√≥n de invitaci√≥n recibida
 */
export async function sendInvitationReceivedNotification(
  recipientUserId: string,
  recipientOrgId: string,
  senderOrgName: string,
  invitationId?: string  // ‚Üê A√ëADIR este par√°metro opcional
) {
  await sendNotification({
    type: 'invitation_received',
    userId: recipientUserId,
    organizationId: recipientOrgId,
    title: 'Nueva invitaci√≥n',
    message: `${senderOrgName} te ha enviado una invitaci√≥n para conectarse.`,
    relatedId: invitationId,  // ‚Üê PASAR invitationId como related_id
    data: { senderOrgName }
  });
}

